@import url('https://fonts.googleapis.com/css2?family=Permanent+Marker&family=Poppins&family=Raleway:ital,wght@0,100;0,200;1,100;1,200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@700&family=Permanent+Marker&family=Poppins&family=Raleway:ital,wght@0,100;0,200;1,100;1,200&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Alfa+Slab+One&family=Patrick+Hand&display=swap');



* {
  border: 0px solid black;
  margin: 0px;
  padding: 0px;

}

body {
  background-color: #efe3d0;
}


.button-wrapper {
  display: flex;
}

.app_bar {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.app_bar_wrapper {

  background-color: #74EBD5;
  background-image: linear-gradient(90deg, #8beedc 0%, #b0bbe9 100%);


}

.app_bar_left_box {
  display: flex;
}

.app_bar_button {
  margin: 0 0.5rem;
}

.basic_search {
  margin-top: 10rem;
}

.basic_search_wrapper {
  background-color: transparent;
  display: flex;
  justify-content: center;
  margin-top: 8vh;
}

.drawer_main {
  flex-grow: 1;
  padding: 0;
}

.drawer_wrapper {
  background-color: #f9fbff;
  height: 100%;
}

.welcome_banner {
  color: #9b1ca6;
  font-size: 2rem;
  font-family: 'Permanent Marker', cursive;
}

.welcome_banner:hover {
  cursor: pointer;
}

.welcome_user {
  font-family: 'Nunito', sans-serif;
  color: white;
  -webkit-text-stroke: 0.6px #fff1f1;
  text-shadow: 0px 0.8px 1px #23430C;
}


.home-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.home_banner {
  width: 100%;
  display: flex;
  justify-content: flex-end;
}

.home-wrapper::before {
  content: "";
  background-color: blue;
  background-image: url("/src/images/pexels-christian-domingues-731022.jpg");
  background-size: 100%;
  background-attachment: fixed;
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  opacity: 0.65;
  z-index: -1;
}


.home_title {
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  width: 100%;
  padding: 0 2vw 0 0;
  height: 55vh;
  margin-top: 6vh;
  font-size: 9rem;
  text-align: right;
  /* color: #d16458; */
  /* color: #3f89bf; */
  color: #e56b6b;
}

h1 {
  font-family: "Avant Garde", Avantgarde, "Century Gothic", CenturyGothic, "AppleGothic", sans-serif;
  font-size: 92px;
  padding: 40px 50px;
  text-align: center;
  text-transform: none;
  text-rendering: optimizeLegibility;
  color: #2c2c2c;
  background-color: transparent;
  letter-spacing: .05em;
  text-shadow:
    4px 4px 0px #d5d5d5,
    7px 7px 0px rgba(0, 0, 0, 0.2);

}

.welcome_logo {
  color: #9b1ca6;
  font-family: 'Permanent Marker', cursive;
}

.card_grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, 400px);
  padding: auto;
  margin: auto;
  column-gap: auto;
  row-gap: 6vh;
  padding-bottom: 10vh;
  justify-content: center;
}

.card {
  width: 345px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.saved_card {
  width: 300px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.card_action {
  height: 100%;
}

.pets_search_title {
  font-size: 4rem;
  text-align: center;
  margin-bottom: 5vh;
  font-family: impact;
  color: #630303;
}

.pets_search_title_empty {
  font-size: 4rem;
  text-align: center;
  margin-bottom: 5vh;
  font-family: impact;

}

.search_page_bar {
  display: flex;
  justify-content: center;
  margin: 5vh 0;
}

.search_logo {
  font-size: 10vh;
  margin-top: -5%;
}

.advanced_search {
  padding-left: 1vw;
  position: relative;

}

.addpet_form_wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

}

.status_dropdown {
  margin-right: 2rem;
}

.card_name_type {
  display: flex;
  justify-content: space-between;
}


.card_status {
  color: purple;
  font-size: 100px;
  position: relative;
  display: float;

}

.pet_page_wrapper {
  background-color: #efe3d0;
  margin: -2%;
  padding: 0;
  height: 130vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pet_page_top {
  display: flex;
  width: 90%;
  justify-content: space-between;

}

.pet_page_top_right {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: end;
  flex-grow: 1;
}


.pet_page_title {
  width: 90%;
  font-family: 'Nunito', sans-serif;
  font-size: 70px;
  padding: 40px 50px;
  text-align: center;
  color: rgb(86 8 8 / 87%)
}

.pet_page_image {
  width: 20vw;
  height: 20vw;
  overflow: hidden;
  margin: 7vh 0 0 10vw;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border-radius: 50%;
  /* background-color: #fefefe; */
  padding: 10px;
  border: 4px solid #ab2d2d;
  background-color: #ffa500;
}

.pet_page_image img {
  width: auto;
  height: 100%;
}

.pet_page_status {
  font-size: 5vh;
  width: 55%;
  color: #9B1CA5;
  font-family: 'Permanent Marker', cursive;
  display: flex;
  margin-right: 8vw;
  justify-content: space-around;
}

.pet_page_bottom {
  display: flex;
  width: 65%;
  justify-content: space-around;
  margin: 10vh 0 10vh 1vw;
  color: rgb(86 8 8 / 87%);
}

.pet_page_bottom_right {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 160%;
  margin-top: -4%;
  width: 25vw;
  font-family: 'Permanent Marker', cursive;
}

.pet_page_stats {
  padding: 10%;
  width: 18vw;
  font-family: 'Permanent Marker', cursive;
}

/* 
.pet_page_health {
  margin-top: 10vh;
} */

.pet_page_bottom_left ul {
  list-style-type: none;
}

.pet_page_bottom_left {
  display: flex;
  align-items: center;
}

.pets_table {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dashboard_wrapper {
  display: flex;
  justify-content: center;
  background-color: #efe3d0;

}

.table_wrapper_user_requests {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  max-width: 80%;

}

.dashboard_title {
  margin: 3vh 0;
  font-family: 'Nunito', sans-serif;
  font-size: 70px;
}

.table_title {
  margin-left: 8vw;
  font-size: 30px;
  margin-bottom: -2vh;
  margin-top: 4vh;
  color: #630303;
  font-family: 'Alfa Slab One', cursive;
}

.table_title_requests {

  font-size: 30px;
  margin-top: 4vh;
  margin-bottom: -2vh;
  color: #630303;
  font-family: 'Alfa Slab One', cursive;
}

.pet_form_selects {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.pet_form_bottom {
  margin-top: 3vh;
}

.photo_upload_container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 200px;
}

.pet_form_button {
  display: flex;
  justify-content: flex-end;
  margin-top: 3vh;
  margin-right: -1vw;
}

.profile_wrapper {
  background-color: #efe3d0;
  margin: -2%;
  padding: 0;
  height: auto;
}

.profile_bottom {
  display: flex;
  justify-content: space-around;
  padding-bottom: 10vh;
  align-items: center;
  font-family: 'Permanent Marker', cursive;
}

.profile_top {
  font-family: 'Nunito', sans-serif;
  padding-top: 6%;
  padding-bottom: 4vh;
  color: #630303;
}

.user_requests_title {
  font-size: 30px;
  margin-left: 10vw;
  margin-bottom: -2vh;
  color: #630303;
  font-family: 'Alfa Slab One', cursive;
}

.person_icon {
  margin-left: -8vw;
  margin-right: 2vw;
  background-color: #9e3ba5;
  border-radius: 50%;
  color: #EFE2D0;
  border: 2px solid #630303;
  width: 95px;
  height: 95px;
}

.missing_pets {
  font-family: 'Nunito', sans-serif;
  width: 60%;
  margin: auto;
  padding-bottom: 14vh;
  display: flex;
  justify-content: center;
  font-size: 20px;

}